"use client";
import { useState, useEffect } from "react";
import Link from "next/link";
import { FiMenu, FiX } from "react-icons/fi";

export default function Navbar() {
  const [navOpen, setNavOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      setScrolled(window.scrollY > 10);
    };
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <header
      className={`fixed top-0 left-0 w-full z-40 transition-all ${
        scrolled ? "backdrop-blur bg-gray-900/60 shadow-lg" : "bg-transparent"
      }`}
    >
      <nav className="max-w-7xl mx-auto flex items-center justify-between px-6 py-4">
        
        {/* LOGO */}
        <Link href="/" className="text-2xl font-bold text-white">
          Vivid<span className="text-blue-500">Seats</span>
        </Link>

        {/* DESKTOP MENU */}
        <div className="hidden md:flex items-center gap-8 text-gray-300 text-sm">
          <Link href="/" className="hover:text-white transition">Home</Link>
          <Link href="/events" className="hover:text-white transition">Events</Link>
          <Link href="/teams" className="hover:text-white transition">Teams</Link>
          <Link href="/about" className="hover:text-white transition">About</Link>

          <button className="px-4 py-2 bg-blue-600 hover:bg-blue-500 rounded-lg text-white">
            Login
          </button>
        </div>

        {/* MOBILE MENU BUTTON */}
        <button
          onClick={() => setNavOpen(!navOpen)}
          className="md:hidden text-white text-2xl"
        >
          {navOpen ? <FiX /> : <FiMenu />}
        </button>
      </nav>

      {/* MOBILE MENU DROPDOWN */}
      <div
        className={`md:hidden bg-gray-900/90 backdrop-blur overflow-hidden transition-all ${
          navOpen ? "max-h-[300px] py-4" : "max-h-0"
        }`}
      >
        <div className="flex flex-col gap-4 px-6 text-gray-300 text-lg">
          <Link href="/" onClick={() => setNavOpen(false)} className="hover:text-white">Home</Link>
          <Link href="/events" onClick={() => setNavOpen(false)} className="hover:text-white">Events</Link>
          <Link href="/teams" onClick={() => setNavOpen(false)} className="hover:text-white">Teams</Link>
          <Link href="/about" onClick={() => setNavOpen(false)} className="hover:text-white">About</Link>
          
          <button className="mt-2 px-4 py-2 bg-blue-600 hover:bg-blue-500 rounded-lg text-white w-fit">
            Login
          </button>
        </div>
      </div>
    </header>
  );
}
